import Player from '../player'

let error = function () {
  let player = this; let util = Player.util; let error = util.createDom('xg-error', '<em class="royplayer-error-text"><span class="royplayer-error-refresh"></span></em>', {}, 'royplayer-error')
  player.root.appendChild(error)
  let text = error.querySelector('.royplayer-error-text')
  let refresh = null
  let refreshListener = false

  function errorFunc () {
    // player.controls.style.display = 'none'
    if (player.error) {
      text.innerHTML = player.error
    } else {
      if (player.config.lang && player.config.lang === 'zh-cn') {
        text.innerHTML = `${player.lang.ERROR}<span class="royplayer-error-refresh"></span>`
      } else {
        text.innerHTML = `${player.lang.ERROR}please try to <span class="royplayer-error-refresh">refresh</span>`
      }
    }

    util.addClass(player.root, 'royplayer-is-error')
    refresh = error.querySelector('.royplayer-error-refresh')
    if (refresh && !refreshListener) {
      ['touchend', 'click'].forEach(item => {
        refresh.addEventListener(item, function (e) {
          e.preventDefault()
          e.stopPropagation()
          let p = e.target || e.srcElement
          if (p && p.tagName.toLocaleLowerCase() === 'span') {
            player.controls.style.display = 'flex'
            player.reload()
          }
        })
      })
      refreshListener = true
    }
  }
  player.on('error', errorFunc)

  function onCanplay () {
    util.removeClass(player.root, 'royplayer-is-error')
  }
  player.on('canplay', onCanplay)
  player.on('error', errorFunc)
  function onDestroy () {
    player.off('error', errorFunc)
    player.off('destroy', onDestroy)
    player.off('canplay', onCanplay)
  }
  player.once('destroy', onDestroy)
}

Player.install('error', error)
